import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Code from '@/components/Code'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/code',
      name: 'Code',
      component: Code
    }
  ]
})



